import React from "react";
import { Button, Card } from "antd";
import "./alterCard.scss";
export default function alterCard(props) {
  const { className, picUrl, title } = props;
  console.log(props);
   function addShop (e){
      console.log(e)
    };
  return (
    <div>
      <Card
        style={{ width: "220px", height: "300px" }}
        className={className}
        hoverable
        cover={<img alt="example" src={picUrl} />}
      >
        <Card.Meta title="Europe Street beat" description={title} />
        <Button type="primary" onClick={addShop}>加入购物车</Button>
      </Card>
    </div>
  );
}
