import React, { Component } from 'react';
import ReactSwiper from 'reactjs-swiper';
import Header from '../../components/head/head'
import Rect from '../../components/rect/rect'
import Category1 from '../../imgs/category1.jpg'

import 'swiper/dist/css/swiper.min.css'

import './home.scss'

class Home extends Component {
  render() {
    const items = [{
        image:  'https://img.bj520.com/201904/aa8848976b.jpg',
        title: '图片1',
        link: 'http://jd.com'
      }, {
        image: 'https://img.bj520.com/201904/9fdd78e6db.jpg',
        title: '图片2',
      }, {
        image:  'https://img.bj520.com/201904/9fdd78e6db.jpg',
        title: '图片3',
        link: 'http://jd.com'
      }, {
        image:  'https://img.bj520.com/201904/9fdd78e6db.jpg',
        title: '图片4',
      }];
    
      const swiperOptions = {
        preloadImages: true,
        autoplay: 4000,
        autoplayDisableOnInteraction: false,
      };
      const imgs=[{
        src:Category1,
        title:' 家庭照'
      
      },{
        src: 'https://img.bj520.com/201812/da6e14261d.jpg',
        title:'亲子照'
      },{
        src: 'https://img.bj520.com/201812/da6e14261d.jpg',
        title:' 婚纱照'
      },{
        src: 'https://img.bj520.com/201812/da6e14261d.jpg',
        title:'个人写真'
      },{
        src: 'https://img.bj520.com/201812/da6e14261d.jpg',
        title:'日系小清新'
      },{
        src: 'https://img.bj520.com/201812/da6e14261d.jpg',
        title:'古风照'
      },{
        src: 'https://img.bj520.com/201812/da6e14261d.jpg',
        title:' 家庭照'
      },{
        src: 'https://img.bj520.com/201812/da6e14261d.jpg',
        title:' 家庭照'
      },
       
      ]
    return (
      <div className="home">
        <Header></Header>
        <ReactSwiper swiperOptions={swiperOptions} showPagination items={items}
                   className="swiper" />
        <div className='Ingenuity'>
           <header>匠心之作</header>
           <div className='content'>
             {imgs.map((item)=>(   
               <div  className='CategoryCard'>
                 <img src={item.src}></img>
                 <span>{item.title}</span>
               </div>))}
           </div>
         
        </div>
        {/* <Rect></Rect> */}
     </div>
    );
  }
}

export default Home;
