import React, { Component } from "react";
import { Input, Icon } from "antd";
import { NavLink, withRouter } from "react-router-dom";
import "./head.scss";

const Search = Input.Search;
const userName = localStorage.getItem("userName");
class Login extends Component {
  render() {
    return (
      <div className="header">
        <NavLink to="/">
          <img src="https://www.hermes.cn/sites/all/themes/custom/hermes/img/hermes-logo.svg" />
        </NavLink>
        <NavLink to="/rent">
          <span>
            <Icon type="shopping" />
          </span>
          <span>服装租赁</span>
        </NavLink>
        <NavLink to="/appreciation">
          <span>
            <Icon type="bars" />
          </span>
          <span>客照欣赏</span>
        </NavLink>

        {/* <span>
          <Search
      placeholder="input search text"
      onSearch={value => console.log(value)}
      style={{ width: 200 }}
    />
          </span> */}
        <NavLink to="/topic">
          <span>
            <Icon type="bars" />
          </span>
          <span>话题讨论</span>
        </NavLink>
        <NavLink to="/cart">
          <span>
            <Icon type="shopping" />
          </span>
          <span>购物车</span>
        </NavLink>

        {!userName ? (
          <NavLink to="/signin" className="login">
            <span>
              <Icon type="user" />
            </span>
            <span>账户</span>
          </NavLink>
        ) : (
          <a
            onClick={() => {
              // localStorage.removeItem("userName");
              console.log(this.props.history.push("/signin"));
            }}
          >
            <span>{userName}</span>
            <span>
              <Icon type="user" className="exit" onClick={() => {}} />
            </span>
          </a>
        )}
      </div>
    );
  }
}

export default withRouter(Login);
