import React, { Component } from "react";
import { Table, Select,Divider, Tag, Button } from "antd";
import Header from "../../components/head/head";
// import {getCartList} from '../../api'
import api from '../../api'
import './shop.scss'

const Option = Select.Option;
class Shop extends Component {
  state={
   cartList : [],
   count:0,
  }
  
  componentDidMount(){
    this.getCart()
  }
  getCart=()=>{
    const  {getCartList} = api
    getCartList().then(
     ({data}) =>{
       this.setState({cartList:data.data.list,
        count:data.data.count,
       })
     }
   )
  }
  postNum=()=>{
    const  {getCartList} = api;
  }
  handleChangNum=(val,option)=>{
    console.log(val,option)
    this.getCart()

  }
  render() {
    const columns = [
      { 
        className:'Pic',
        title: "全选",
        dataIndex: "all",
        key: "all",
        render: text => <img src={text}></img>
      },
      {
        title: "商品信息",
        dataIndex: "info",
        key: "info",
        render:text =><>
                       <span>{text}</span>
                        <span>删除</span>
                       </>
      },
      {
        title: "单价",
        dataIndex: "price",
        key: "price"
      },
      {
        title: "数量",
        key: "nums",
        dataIndex: "nums",
        render: text => (
         <Select defaultValue={text} style={{ width: 60 }} onChange={this.handleChangNum}>
          <Option value="1">{text}</Option>
          <Option value="2">2</Option>
          <Option value="3">3</Option>
          <Option value="4">4</Option>
          <Option value="5">5</Option>
         </Select>
        )
      },
      {
        title: "总计",
        key: "totalPrice",
        dataIndex: "totalPrice",
      }
    ];
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(
          `selectedRowKeys: ${selectedRowKeys}`,
          "selectedRows: ",
          selectedRows
        );
      },
      getCheckboxProps: record => ({
        disabled: record.name === "Disabled User", // Column configuration not to be checked
        name: record.name
      })
    };
    return (
      <section className="shopCar">
        <Header />
        <section class='content'>
        <header> 
          <span className='goShop' onClick={()=>{this.props.history.push('/')}}>&lt;继续购物</span>
          <span>我的购物袋({this.state.count})</span>
        </header>
        <div className='box'>
        <section className="table">
            <Table
              rowSelection={rowSelection}
              columns={columns}
              dataSource={this.state.cartList}
              pagination = {false}
            />
        </section>
        <section className='account'>
          <div>订单总结</div>
          <Button>立即结算</Button>
          <span>商品价格:￥4444</span>
          <span>总价￥4444</span>
        </section>
        </div>
        </section>
      </section>
    );
  }
}
export default Shop;
